.mini-index {
  padding: 12px 20px 20px;
}
.mini-index .home-decorate-container {
  min-height: 300px;
  overflow: hidden;
}
.mini-index .home-decorate-container .custom-page-block {
  width: 220px;
  height: 392px;
  border: 1px solid #DCDEE0;
  margin: 0px 10px 40px 0px;
}
.mini-index .home-decorate-container .custom-page-block .current-home-box {
  position: absolute;
  width: 100%;
  height: 100%;
}
.mini-index .home-decorate-container .custom-page-block .current-home-box .current-home {
  position: absolute;
  top: 20px;
  color: white;
  padding-left: 5px;
  height: 24px;
  line-height: 24px;
  width: 65px;
  background-color: #4e7be5;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
  font-size: 12px;
}
.mini-index .home-decorate-container .custom-page-block .name {
  text-align: center;
  height: 35px;
  line-height: 35px;
  font-size: 12px;
  color: #333333;
  position: absolute;
  bottom: -40px;
  background: none;
  border: none;
  width: 220px;
}
.mini-index .home-decorate-container .custom-page-block .mask {
  position: absolute;
  width: 100%;
  height: 292px;
  padding-top: 100px;
}
.mini-index .home-decorate-container .custom-page-block .add {
  position: absolute;
  width: 220px;
  height: 392px;
}
.mini-index .home-decorate-container .custom-page-block .add .el-icon-plus {
  margin-top: 156px;
  font-size: 80px;
}
.mini-index .home-decorate-container .current-template {
  display: inline-block;
  float: left;
}
.mini-index .home-decorate-container .current-template img {
  width: 220px;
  background: #f2f4f7;
  border-radius: 4px;
  padding: 4px;
}
.mini-index .home-decorate-container .current-template .box {
  overflow: hidden;
  margin-top: 10px;
  padding: 0px 5px;
}
.mini-index .home-decorate-container .current-template .box .left {
  display: inline-block;
  float: left;
}
.mini-index .home-decorate-container .current-template .box .right {
  display: inline-block;
  float: right;
}
.mini-index .home-decorate-container .current-template .box .right .blue {
  color: #2e8dff;
  margin-left: 20px;
  cursor: pointer;
}
.mini-index .optional-template {
  margin-top: 30px;
}
.mini-index .optional-template .title {
  font-size: 14px;
  color: #333333;
  line-height: 20px;
  padding: 10px 20px;
  background: #F5F7Fa;
}
.mini-index .optional-template .content {
  margin-top: 17px;
}
.mini-index .optional-template .content .template-box {
  width: 80px;
  text-align: center;
  display: inline-block;
}
.mini-index .optional-template .content .template-box .check {
  border: 1px solid #fb6838;
}
.mini-index .optional-template .content .template-box .text {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  text-align: center;
}
.mini-index .optional-template .content .template-box .tab {
  display: inline-block;
  box-sizing: border-box;
  width: 79px;
  height: 79px;
  line-height: 76px;
  text-align: center;
  margin-right: 20px;
  cursor: pointer;
}
.mini-index .optional-template .content .template-box .tab .box {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  width: 65px;
  height: 65px;
  line-height: 65px;
  background: #ffffff;
}
.mini-index .optional-template .content .template-box .tab .box:first-child {
  border: 1px solid #dddddd;
}
.mini-index .optional-template .content .template-box .tab .custom-tab {
  border: 1px solid #2e8dff;
  color: #2e8dff;
  box-sizing: border-box;
  text-align: center;
  overflow: hidden;
  font-size: 45px;
  cursor: pointer;
}
.mini-index .optional-template .content .template-box .tab:last-child {
  margin-right: 0px;
}
.mini-index .library-tab .library-tab-item.el-button {
  border: 1px solid #dcdfe6;
  color: #606266;
}
.mini-index .library-tab .library-tab-item.el-button.active {
  border: 1px solid #256eff;
  color: #256eff;
}
.mini-index .mini-index-dialog {
  height: 250px;
  overflow-y: auto;
}
.mini-index .mini-index-dialog .custom-page-block {
  margin-right: 15px;
}
.mini-index .mini-index-dialog .custom-page-block .mask {
  display: block;
}
.page-box {
  margin: 20px 0;
}
