.template-decorate-container {
  min-height: 300px;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}
.template-decorate-container .custom-page-wrapper {
  margin-right: 10px;
  position: relative;
  width: 222px;
  display: inline-block;
}
.template-decorate-container .custom-page-wrapper .bg-content {
  width: 222px;
  height: 394px;
  border: 1px solid #DCDEE0;
  position: relative;
  box-sizing: border-box;
}
.template-decorate-container .custom-page-wrapper .bg-content .current-home-box {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #f35f28;
  color: #ffffff;
  text-align: center;
}
.template-decorate-container .custom-page-wrapper .btn-box {
  padding: 6px 0;
  display: flex;
}
.template-decorate-container .custom-page-wrapper .btn-box.btn-box-center {
  justify-content: center;
}
.template-decorate-container .custom-page-wrapper .btn-box .btn {
  color: #2e8dff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  margin-left: 14px;
}
.template-decorate-container .custom-page-wrapper .name {
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  color: #333333;
  background: none;
  border: none;
  width: 220px;
}
.template-decorate-container .custom-page-wrapper .name .edit-name-box {
  display: flex;
}
.template-decorate-container .custom-page-wrapper .name .edit-name-box .edit-name-input {
  flex: 1;
}
.template-decorate-container .custom-page-wrapper .name .edit-name-box .btn {
  width: 36px;
  color: #2e8dff;
  margin-left: 6px;
  text-align: center;
  font-size: 13px;
  cursor: pointer;
}
.template-decorate-container .custom-page-wrapper .name .edit-icon {
  color: #2e8dff;
  margin-left: 10px;
  cursor: pointer;
}
.template-decorate-container .custom-page-wrapper .name .name-text-box {
  display: flex;
  align-items: center;
}
.template-decorate-container .custom-page-wrapper .name .name-text-box .name-text {
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.template-decorate-container .custom-page-wrapper .add {
  height: 100%;
  width: 100%;
  font-size: 13px;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}
.template-decorate-container .custom-page-wrapper .add .el-icon-plus {
  font-size: 80px;
}
