.home-decorate-tips {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #333333;
}
.home-decorate-tips > span {
  padding: 8px 0px;
}
.home-decorate-tips > span:first-child {
  padding-top: 0;
}
.home-decorate-container {
  margin-top: 10px;
}
.custom-page-block {
  width: 160px;
  height: 196px;
  border: 1px solid #DCDEE0;
  border-radius: 4px;
  position: relative;
  float: left;
  margin: 0 10px 10px 0;
  background-size: cover !important;
}
.custom-page-block .preview {
  height: 160px;
  position: relative;
}
.custom-page-block .preview .current-home {
  position: absolute;
  top: 20px;
  color: white;
  padding-left: 5px;
  height: 24px;
  line-height: 24px;
  width: 65px;
  background-color: #4e7be5;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}
.custom-page-block .add {
  text-align: center;
  cursor: pointer;
}
.custom-page-block .add .el-icon-plus {
  margin-top: 53px;
  color: #333333;
  font-size: 45px;
}
.custom-page-block .name {
  text-align: center;
  height: 35px;
  line-height: 35px;
  font-size: 12px;
  color: #333333;
  background: #F5F7Fa;
  border-top: 1px solid #DCDEE0;
}
.custom-page-block .mask {
  position: absolute;
  width: 160px;
  height: 196px;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.7);
  padding-top: 30px;
  display: none;
}
.custom-page-block .mask .oper-item {
  cursor: pointer;
  height: 22px;
  width: 80px;
  text-align: center;
  margin: 10px auto 0;
  border-radius: 4px;
  border: 1px solid #DCDEE0;
  background-color: #fff;
}
.custom-page-block .mask .oper-item:hover {
  border: 1px solid rgba(215, 224, 234, 0.8);
  color: rgba(86, 105, 128, 0.8);
}
.custom-page-block:hover .mask {
  display: block;
}
