.custom-page {
  padding: 12px 20px 20px;
}
.custom-page .home-decorate-container {
  min-height: calc(100vh - 400px);
}
.custom-page .home-decorate-container .custom-page-block .mask {
  box-sizing: border-box;
}
.custom-page .library-tab {
  padding: 10px 0;
}
.custom-page .library-tab .library-tab-item.el-button {
  border: 1px solid #dcdfe6;
  color: #606266;
}
.custom-page .library-tab .library-tab-item.el-button.active {
  border: 1px solid #256eff;
  color: #256eff;
}
.custom-page .custom-page-wrapper {
  float: left;
}
.custom-page .custom-page-wrapper .edit-name {
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  color: #333333;
  background: none;
  border: none;
  width: 160px;
}
.custom-page .custom-page-wrapper .edit-name .edit-name-box {
  display: flex;
}
.custom-page .custom-page-wrapper .edit-name .edit-name-box .edit-name-input {
  flex: 1;
}
.custom-page .custom-page-wrapper .edit-name .edit-name-box .btn {
  width: 30px;
  color: #2e8dff;
  margin-left: 4px;
  text-align: center;
  font-size: 12px;
  cursor: pointer;
}
.custom-page .custom-page-wrapper .edit-name .edit-icon {
  color: #2e8dff;
  margin-left: 10px;
  cursor: pointer;
}
.custom-page .custom-page-wrapper .edit-name .name-text-box {
  display: flex;
  align-items: center;
}
.custom-page .custom-page-wrapper .edit-name .name-text-box .name-text {
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.custom-page .search {
  width: 300px;
  display: flex;
  padding: 10px 0;
}
.name {
  overflow: hidden;
}
