.produce-container {
  width: 100%;
  min-height: calc(100vh - 145px);
  min-width: 990px;
  position: relative;
  padding: 20px 0px 20px 20px;
  box-sizing: border-box;
}
.produce-container .custom-decorate-tips {
  margin-bottom: 10px;
}
.produce-container .components-area {
  width: 210px;
  overflow-y: auto;
  background-color: #fff;
  display: inline-block;
  vertical-align: top;
}
.produce-container .components-area div:first-child .ca-title {
  padding-top: 0px;
}
.produce-container .components-area .dss-btn-primary {
  margin: 30px 50px;
}
.produce-container .components-area .ca-title {
  font-family: PingFangSC-Regular;
  padding: 10px 0 4px 10px;
  height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #20252c;
}
.produce-container .components-area .components-container {
  /* border: 1px solid rgba(233, 236, 241, 1);
      border-right: 0;
      border-bottom: 0; */
  line-height: 0;
  margin-left: 1px;
}
.produce-container .components-area .drag-class {
  border: 1px solid #4e7be5;
  background-color: white;
}
.produce-container .components-area .x {
  margin: 0px !important;
  width: 69px;
  height: 69px;
  border: 1px solid #e9ecf1;
  border-left: 0;
  border-top: 0;
  position: relative;
  display: inline-block !important;
}
.produce-container .components-area .x .item {
  width: 100%;
  height: 100%;
  position: relative;
}
.produce-container .components-area .x .item .item-cover {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  top: 0px;
}
.produce-container .components-area .x .img {
  width: 69px;
  height: 22px;
  line-height: 22px;
  text-align: center;
  padding-top: 8px;
}
.produce-container .components-area .x .name {
  height: 17px;
  line-height: 17px;
  font-size: 12px;
  font-weight: 400;
  color: #858c99;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}
.produce-container .components-area .x:nth-child(-n + 3) {
  border-top: 1px solid #e9ecf1;
}
.produce-container .components-area .x:nth-child(3n + 1) {
  border-left: 1px solid #e9ecf1;
}
