.produce-container .screen-area {
  background: url(/resources/dss-web-portal/content/image/phone.185738a7.png) no-repeat 100% 100%;
  background-size: cover;
  width: 375px;
  height: 667px;
  overflow-y: hidden;
  display: inline-block;
  vertical-align: top;
  border: 1px solid rgba(230, 230, 230, 0.5);
}
.produce-container .screen-area .header {
  margin-top: 24px;
  cursor: pointer;
}
.produce-container .screen-area .screen {
  width: 375px;
  height: 602px;
  position: relative;
  overflow: hidden;
}
.produce-container .screen-area .screen .screen-container::-webkit-scrollbar {
  display: none;
}
.produce-container .screen-area .screen .screen-container {
  width: 395px;
  height: 602px;
  position: absolute;
  left: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
.produce-container .screen-area .screen .screen-container .screen-x {
  position: relative;
  overflow: hidden;
  width: 375px;
}
.produce-container .screen-area .screen .screen-container .screen-x > .field {
  box-sizing: border-box;
}
.produce-container .screen-area .screen .screen-container .screen-x .cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 1px dashed #979797;
  display: none;
  cursor: move;
}
.produce-container .screen-area .screen .screen-container .screen-x .cover .com-close {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}
.produce-container .screen-area .screen .screen-container .screen-x .select,
.produce-container .screen-area .screen .screen-container .screen-x:hover .cover {
  display: block;
}
.produce-container .screen-area .screen .drag {
  background-color: lightgrey;
  overflow: hidden;
  cursor: move;
}
.produce-container .editor-area-wrapper {
  width: 367px;
  min-height: 667px;
  overflow: hidden;
  position: relative;
  display: inline-block;
  border: 1px solid rgba(230, 230, 230, 0.5);
}
.produce-container .editor-area-wrapper .editor-area::-webkit-scrollbar {
  display: none;
}
.produce-container .editor-area-wrapper .editor-area {
  width: 387px;
  min-height: 667px;
  overflow-y: auto;
  background: #ffffff;
  border-radius: 0 4px 4px 0;
}
